
import util from '@/libs/util'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default{
mixins: [common,globals],
components: {
Cell,
Badge,
Group,
Flexbox,
CellBox,
Divider,
FlexboxItem,
},
data () {
return {
user: {},
students: []
}
},
mounted () {
this.get_students()
this.initGlobal('lesson_config')
},
computed: {
my_menu () {
let list = this.$store.state.ui_config.my_menu||[]
return list
}
},
methods: {
get_students () {
let user_info = this.$store.state.user.info
this.$http.get('students?uid='+user_info.uid)
.then(response=>{
this.students = response.body.data.list
this.$store.commit('updateUserInfo',{user: this.students,action:'updateArchives'})

if(user_info.default_sid !== 0){
this.select_student(user_info.students.find(s=>s.sid===user_info.default_sid))
} else if(user_info.default_sid===0 && this.students.length > 0) {
this.select_student(this.students[0])
}
this.init_student_info()
})
},
select_student (student) {
let user_info = this.$store.state.user.info
let uri = util.sprintf('users/switch_sid?uid=%s&sid=%s',this.$store.state.user.info.uid,student.sid)
this.sapi('post',uri,{sid:student.sid})
.then(response=>{
this.$store.commit('setArchiveInfo',student.sid)
this.user = user_info.students.find(s=>s.sid===user_info.default_sid)
})
.catch(response=>{
this.alert('错误提示',response.body.message,true)
})
},
init_student_info () {
let [user_info,$router] = [this.$store.state.user.info,this.$router]

if(user_info.students.length===0){
this.$vux.alert.show({
title: '提示',
content: '您还没有绑定学员，去绑定一个?',
onShow () {
console.log('Plugin: I\'m showing')
},
onHide () {
$router.push({path:'./add'})
}
})
}
if(user_info.default_sid !== 0 && user_info.students.length>0){
this.user = user_info.students.find(s=>s.sid===user_info.default_sid)
}
else{
this.user = user_info
}
},
match_grade (grade){
if(typeof this.lesson_config !== 'undefined' && typeof this.lesson_config.grade.find(g=>g.id===grade) !== 'undefined'){
return this.lesson_config.grade.find(g=>g.id===grade).name
}
return ''
}
}

}



